.charging{
  width: 100%;
  height: 100vh;
  background-color: black;
  position: absolute;
  z-index: 3000;
  top: 0px;
  left: 0px;
}

.charging .battery{
  position: absolute;
  border: 3px solid white;
  height: 70px;
  width: 200px;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  border-radius: 10px;
  
}

.battery .juice{
  height: 70px;
  width: 100px;
  background: linear-gradient(to bottom, rgba(180,227,145,1) 0%,rgba(97,196,25,1) 50%,rgba(180,227,145,1) 100%,rgba(180,227,145,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  animation: charging 2s infinite;
}

.charging .battery::after{
  content: ' ';
  border: 5px solid white;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  position: relative;
  top: -45px;
  right: -100%;
}

.charging .fa-plug{
  color: rgb(214, 214, 214);
  font-size: 50px;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 80%;
  left: 50%;
}

@keyframes charging{
  90%{
    width: 200px;
  }
  100%{
    width: 100px;
  }
}