.themes-settings {
  width: 430px;
  height: 260px;
  background: rgb(206, 206, 206);
  border-radius: 10px;
  overflow: hidden;
  background-image: url('../../Assets/themes.png');
  background-repeat: no-repeat;
  background-size: 40%;
  background-position-x: 300px;
  background-position-y: 150px;
}

.themes-settings .title{
  background: rgb(0, 0, 0)
}

.themes-settings .info{
  color: black;
  text-align: center;
  margin-top: 20px;
  line-break: strict;
}

.themes-settings .select{
  text-align: center;
  font-weight: 600;
  background: white;
  width: 300px;
  padding: 4px 35px 4px 35px;
  border-radius: 10px;
  position: absolute;
  top: 48%;
  font-size: 18px;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 2px solid black;
}

.themes-settings .select::after{
  content: ">";
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}

.themes-settings .select::before{
  content: "<";
  position: absolute;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
}

.themes-settings img{
  width: 150px;
  position: absolute;
  top: 165px;
  left: 165px;
  border-radius: 5px;
  border: 2px solid black;
}