.auto-result{
  width: 100%;
  height: 91%;
  background: #000;
}

.canvas-container{
  width: 60%;
  height: 100%;
  background: #000;
  
}

.lines-container{
  position: absolute;
  color: white;
  right: 0px;
  top: 22px;
  z-index: 9;

}

.lines-container .line{
  margin-top: 5px;
  width: 190px;
  height: 35px;
  display: flex;
  align-items: center;

  z-index: 9;
}

.line .line-label{
  background: rgb(185, 185, 185);
  height: 100%;
  width: 35px;
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  color: black;
  font-weight: 600;
  z-index: 9;

}

.line .line-data{
  background: rgb(78, 78, 78);
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 12px;

}

.line .indicator{
  width: 13px;
  height: 13px;
  background: red;
  position: absolute;
  right: 10px;
  border-radius: 50%;
}

.res-preloader{
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  background: #000;
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  transition: transform 1s;

}

.res-save-popup-container{
  width: 100vw;
  height: 91vh;
  background: rgba(0, 0, 0, 0.699);
  position: absolute;
  z-index: 1000;
  display: none;
  justify-content: center;
  align-items: center;
}

.res-save-popup{
  width: 360px;
  height: 160px;
  background: rgb(105, 105, 105);
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.res-save-popup .fas{
  color: white;
  font-size: 40px;
}

.res-save-popup span{
  margin-top: 10px;
  font-weight: 500;
  color: white;
}

.res-popup-buttons{
  width: 250px;
  display: flex;
  justify-content: space-around;
}

.res-button{
  border: 2px solid rgb(97, 97, 97);
  padding: 5px 15px 5px 15px;
  background: rgb(177, 177, 177);
  border-radius: 10px;
  margin-top: 10px;
  font-size: 16px;
  font-weight: 600;
  color: black;
}

.res-button.selected{
  background: rgb(27, 223, 27) !important;
  border: 2px solid rgb(97, 97, 97) !important; 
}