.resistivity-calibration {
  width: 100vw;
  height: 91vh;
  background: rgb(218, 218, 218);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.calibration-image {
  width: 420px;
  height: 120px;
}

.calibration-image p{
  font-size: 13px;
}

.calibration-button {
  width: 320px;
  height: 30px;
  background: rgb(75, 75, 75);
  margin-top: 10px;
  border-radius: 10px;
  color: white;
  font-weight: 400;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  transition: all 0.3s
}

.calibration-line {
  position: absolute;
  margin-top: 30px;
  width: 420px;
  height: 8px;
  background: rgb(153, 153, 153);
  top: 230px;
}

.calibration-selected {
  background: rgb(10, 163, 43) !important;
}

.calibration-letter{
  width: 20px;
  text-align: center;
  border-radius: 5px;
  margin-top: 20px;
}

.calibration-letter-selected{
  background: rgb(65, 252, 41);
  font-weight: 600;
  color: black;
  position: relative;

}


.calibration-letter-holder{
  width: inherit;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 10px;
}

.prop-nums{
  font-size: 10px;
  width: 25px;
  display: block;
  color: rgb(0, 0, 0) !important;
  position: relative;
  left: -2px;
  top: -40px;
}

.calibration-popup{
  width: 120px;
  height: 120px;
  position: absolute;
  background: rgb(255, 255, 255);
  z-index: 1200;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.75); 
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.calibration-popup p{
  font-size: 12px;                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                
}

.calibration-preloader{
  width: 50px;
  height: 50px;
  border: 2px dotted black;
  border-radius: 50%;
  animation: calib-rotate 2s infinite;
}

@keyframes calib-rotate{
  100%{
    transform: rotate(360deg);
  }
}

.calibration-reset-popup{
  width: 220px;
  height: 80px;
  background: rgb(255, 255, 255);
  position: absolute;
  border-radius: 10px;
  z-index: 1200;
  display: none;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.75); 
}

