.hardware-info {
  width: 100vw;
  height: 91vh;
  background: rgba(255, 255, 255, 0.76);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hardware-info-container {
  border: 1px solid black;
  width: 440px;
  height: 210px;
  border-top: none;
  background: rgba(32, 32, 32, 1);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.tabs-container {
  display: flex;
  width: 442px;
  justify-content: space-around;
  height: 40px;
  align-items: center;
}

.tabs-container .tab {
  border: 1px solid black;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-bottom: none;
}

.tabs-container .tab:first-child {
  border-right: none;
}

.selected-tab {
  background: linear-gradient(0deg, rgba(32, 32, 32, 1) 0%, rgb(67, 83, 71) 77%, rgb(36, 92, 48) 100%);
  font-weight: 600;
  color: white;
}

.hardware-info-tab {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.info-title {
  font-size: 12px;
  margin-bottom: 3px;
  margin-top: 3px;
}

.info-value {
  font-size: 12px;
  background: rgb(189, 189, 189);
  width: 100%;
  text-align: center;
  height: 15px;
  padding-top: 3px;
}

.info-item {
  width: 90px;
  height: 90px;
  border-radius: 5px;
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
  background: white;
  opacity: 0.9;
}

.hardware-info .info-icon {
  width: 45px;
}

.info-icon-holder {
  width: 45px;
  height: 45px;
  overflow: hidden;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.system-status-tab {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.status {
  width: 60px;
  height: 180px;
  background: white;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
}

.status-title {
  font-size: 12px;
  margin-top: 3px;
}

.status-graphic {
  width: 45px;
  height: 130px;
  justify-content: flex-start;
  display: flex;
  align-items: center;
  padding-left: 15px;
}

.status-value {
  font-size: 12px;
  margin-bottom: 3px;
  border-top: 1px solid black;
  padding-top: 3px;
}

.status-graphic .status-line{
  width: 8px;
  height: 120px;
  background: rgb(187, 187, 187);
  display: flex;
  align-items: flex-end;
}

.status-line .line-value{
  background: rgb(21, 182, 0);
  height: 100%;
  width: 100%;
  transition: all 120ms;
}

.line-indicator{
  font-size: 12px;
  margin-left: 12px;
  margin-top: -5px;
}