.popup-settings {
  width: 60%;
  height: 45%;
  position: absolute;
  background: rgb(68, 68, 68);
  top: 25%;
  left: 20%;
  z-index: 1200;
  border-radius: 20px;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.85);
  opacity: 0.95;
  display: flex;
  flex-direction: column;
  opacity: 0;
  transform: scale(0.8);
  transition: all 300ms;
  transition-timing-function: cubic-bezier(0.175, 0.885, 0.62, 2.475);
  overflow: hidden;
}

.popup-title{
  color: rgb(255, 255, 255);
  align-self: center;
  background: rgb(112, 112, 112);
  margin-bottom: 25px;
  width: 100%;
  text-align: center;
  padding: 10px 0px 10px 0px;
}

.popup-button{
  align-self: center;
  border: 2px solid rgb(36, 161, 19);
  padding: 5px 30px 5px 30px;
  border-radius: 10px;
  color: white;
  position: relative;
}

.popup-button::before{
  content: "<";
  position: absolute;
  left: 10px;
}

.popup-button::after{
  content: ">";
  position: absolute;
  right: 10px;
}

.background{
  width: 100%;
  height: 100vh;
  top: 0px;
  left: 0px;
  z-index: 1000;
  opacity: 0.7;
  background: #0000009c;
  position: absolute;
}

.popup-settings .info{
  color: rgb(216, 216, 216);
  font-size: 13px;
  margin: 0px auto;
  margin-top: 15px;
}