.ground-scan {
  width: 100vw;
  height: 91vh;
  /* background: rgb(68, 68, 68); */
}

.scan-properties {
  opacity: 0.1;
  transition: all 300ms;
  background: rgba(0, 0, 0, 0.68);
  width: 100%;
  height: 100%;
  overflow: hidden;
  color: white;
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
}

.mode-selected {
  background: rgb(36, 161, 19) !important;
  font-weight: 800 !important;
  border-radius: 5px;
  color: "white" !important;
}

.select-box-container {
  width: 350px;
  background: rgb(49, 49, 49);
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  overflow: hidden;
  padding-bottom: 4px;
  padding: 4px 20px 4px 20px;
}

.select-box {
  width: 350px;
  height: 27px;
  line-height: 26px;
  font-size: 16px;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.select-box .name {
  float: right;
  width: 150px;
  text-align: center;
  height: 22px;
  margin-top: 3px;
  line-height: 22px;
  border-radius: 5px;
  margin-right: 10px;
  text-transform: uppercase;
  font-weight: 600;
}

.select-box .label {
  float: left;
  margin-left: 10px;
  margin-right: 30px;
}

.scan-properties .title {
  width: 390px;
  height: 10px;
  margin: 0px auto;
  margin-top: 20px;
  font-weight: 900px;
  line-height: 10px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background: rgb(32, 32, 32);
}

.select-box .name::after {
  content: ">";
  color: #fff;
  margin-left: 15px;
  opacity: 0.4;
}

.select-box .name::before {
  content: "<";
  color: #fff;
  margin-right: 15px;
  opacity: 0.4;
}

.ground-scan .info {
  background: rgb(36, 161, 19) !important;
  padding: 2px 8px 2px 8px;
  margin-top: 7px;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
  width: 300px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
}

.ground-scan .info:after {
  animation: shine 3s ease-in-out infinite;
  animation-fill-mode: forwards;
  content: "";
  position: absolute;
  top: -110%;
  left: -210%;
  width: 200%;
  height: 200%;
  opacity: 0;
  transform: rotate(30deg);
  background: rgba(255, 255, 255, 0.13);
  background: linear-gradient( to right, rgba(255, 255, 255, 0.13) 0%, rgba(255, 255, 255, 0.13) 77%, rgba(255, 255, 255, 0.5) 92%, rgba(255, 255, 255, 0.0) 100%);
}

@keyframes shine {
  10% {
    opacity: 1;
    top: -30%;
    left: -30%;
    transition-property: left, top, opacity;
    transition-duration: 0.7s, 0.7s, 0.15s;
    transition-timing-function: ease;
  }
  100% {
    opacity: 0;
    top: 30%;
    left: 30%;
    transition-property: left, top, opacity;
  }
}