.resistivity-manual-result{
  background: #000;
  width: 100vw;
  height: 91vh;
  background-image: url(../../Assets/res_manual.png)
}

.resistivity-manual-result .right-panel{
  position: absolute;
  background: rgb(78, 78, 78);
  width: 200px;
  height: 150px;
  right: 0px;
  top: 60px;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  border: 2px solid rgb(206, 206, 206);
  border-right: none;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: center;
}

.resistivity-manual-result .result-panel{
  background: rgb(197, 197, 197);
  display: flex;
  width: 140px;
  padding: 5px 14px 5px 14px;
  font-size: 13px;
  height: 20px;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-radius: 5px;
}

.resistivity-manual-result .result-panel-value{
  font-weight: 600;
}

.canvas-container-manual{
  width: 100%;
  height: 100vh;
}

