.activation {
  background: rgb(243, 243, 243);
  width: 100%;
  height: 91%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: center;
  align-items: center;
  transform: scale(2);
  transition: all 400ms;
  opacity: 0.1;
}

.activation svg {
  margin-top: 20px;
}

.activation .device-id {
  background: #000;
  color: white;
  font-size: 25px;
  padding: 10px 20px 10px 20px;
  letter-spacing: 10px;
  margin-top: 10px;
  font-weight: 800;
  border-radius: 15px;
  border: 2px solid white;
}

.device-id-info{
  color: rgb(27, 27, 27);
  margin-top: 10px;
  font-weight: 600;
}