.turning-off{
  width: 100vw;
  height: 100vh;
  position: absolute;
  background: white;
  z-index: 1000000;
  top: 0px;
  left: 0px;
  right: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 500ms;
}

.turning-off .fa-power-off{
  font-size: 30px;
}

.turning-off .rotating-ring{
  width: 40px;
  height: 40px;
  border: 10px solid black;
  border-radius: 50%;
  position: absolute;
  animation: rotatingRing 1s linear infinite;
  border-image: linear-gradient(to left, black, black);
}

@keyframes rotatingRing{
  0%{
    transform: rotate(0deg) scale(1);
  }
  50%{
    transform: rotate(180deg) scale(1.2);
  }
  100%{
    transform: rotate(360deg) scale(1);
  }
}