.sound-settings {
  width: 100vw;
  height: 91vh;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: scale(2);
  opacity: 0.1;
  transition: 200ms;
}

.sound-settings .sound-settings-popup {
  background: rgb(68, 68, 68);
  width: 350px;
  height: 200px;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.85);
  border-radius: 20px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.slider-container {
  background: rgb(77, 77, 77);
  flex-direction: row;
  display: flex;
  justify-content: center;
  align-items: center;

  width: 320px;
  height: 60px;
  border-radius: 10px;
  margin-top: 10px;
}

.volume-slider {
  background: rgb(255, 255, 255);
  width: 220px;
  height: 4px;
  margin-top: 0px;
  border: 2px solid black;
}

.volume-icon {
  transform: translate(-13px, 5px);
  font-size: 30px;
  color: white;
  margin-top: -10px;
}

.volume-pin {
  background: rgb(255, 255, 255);
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 2px solid black;
  transform: translate(-7px, -7px);
  transition: all 100ms;
}

.volume-indicator{
  color: white;
  font-size: 13px;
  margin-top: 20px;

}

.sound-settings .selected{
  background: rgb(126, 126, 126) !important;
}

.checkbox{
  color: white;
  font-size: 23px;
  margin-right: 10px;
}

.vibration{
  color: white;
  font-weight: 400;
  margin-top: 3px;
}