.turn-off{

  width: 100%;
  height: 91%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transform: scale(3);
  opacity: 0.1;
  transition: all .5s;
  
}

.turn-off-popup{
  width: 420px;
  height: 230px;
  background: rgb(68, 68, 68);
  border-radius: 10px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.turn-off-question{
  color: white;
  text-align: center;
  font-size: 18px;
  margin: 0px 20px 20px 20px;
}

.turn-off-button-container{
  width: 350px;
  height: 40px;
  display: flex;
  justify-content: space-between;
}

.turn-off-button{
  width: 150px;
  height: 30px;
  border: 2px solid white;
  border-radius: 10px;
  text-align: center;
  line-height: 30px;
  color: white  !important;
  font-size: 22px;
}

.turn-off .fa-power-off{
  font-size: 30px;
  color: white;
  margin-bottom: 30px;
}

.turn-off-button.selected{
  background: rgb(36, 161, 19) !important;
  border: 2px solid white !important;

}