.viewer {
  width: 100vw;
  height: 91vh;
  background: #000;
}

.viewer iframe {
  border: none;
  width: 100vw;
  margin: 0px auto;
  height: 91vh;
  position: absolute;
  top: 0px;
  z-index: 0;
  /* filter: hue-rotate(120deg) */
}

.viewer-preloader {
  width: 100vw;
  height: 91vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  z-index: 120000;
  background-color: #000;
}

.square {
  width: 50px;
  height: 50px;
  background: rgb(255,0,0);
  background: radial-gradient(circle, rgba(255,0,0,0.9976365546218487) 0%, rgba(247,249,0,1) 24%, rgba(26,231,26,1) 49%, rgba(147,242,244,1) 59%, rgba(0,78,255,1) 100%);
  animation: rotate-square 2s infinite;
  position: relative; 
  z-index: 1000;
}

.preloader-text {
  margin-top: 15px;
  color: #fff;
  font-weight: 600;
}

@keyframes rotate-square {
  0% {
    transform: rotateY(0deg) rotateX(0deg);
  }
  50% {
    transform: rotateY(180deg) rotateX(180deg);
  }
  100% {
    transform: rotateY(0deg) rotateX(0deg);
  }
}

.overlay{
  display: none;
  width: 100vw;
  height: 100vh;
  background-image: url('../../Assets/overlay.png');
  position: absolute;
  top: 0px;
  left: 0px;
}

.viewer .title{
  position: absolute;
  top: 0px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
}