.background-color {
  width: 430px;
  height: 260px;
  background: rgba(255, 255, 255, 0.486);
  border-radius: 10px;
  overflow: hidden;
  background-image: url('../../Assets/themes.png');
  background-repeat: no-repeat;
  background-size: 40%;
  background-position-x: 300px;
  background-position-y: 150px;
}

.background-color .title{
  background: rgb(0, 0, 0)
}

.background-color .info{
  color: black;
  text-align: center;
  margin-top: 20px;
  line-break: strict;
}

.background-color .select{
  text-align: center;
  font-weight: 600;
  background: white;
  width: 300px;
  height: 22px;
  padding: 4px 35px 4px 35px;
  border-radius: 10px;
  font-size: 18px;
  border: 2px solid black;
}

.background-color .select::after{
  content: ">";
  right: 80px;
  position: absolute;
}

.background-color .select::before{
  content: "<";
  left: 80px;
  position: absolute;
}

.background-color img{
  width: 150px;
  position: absolute;
  top: 165px;
  left: 165px;
  border-radius: 5px;
  border: 2px solid black;
}

.background-color .select-container{
  display: flex;
  height: 130px;
  margin-top: 20px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.background-color .select.selected{
  background: rgb(36, 161, 19) !important;
  border: 2px solid black !important;
  color: white !important;
}

.background-color .select-title{
  background: black;
  color: white;
  padding: 3px 10px 3px 10px;
  border-radius: 3px;
}

.background-color .confirm{
  width: 330px;
  height: 120px;
  position: absolute;
  background: rgb(36, 161, 19) ;
  z-index: 1000;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 20px;
  border: 2px solid black;
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: center;
  color: white;
}

.confirm .fa-check{
  font-size: 40px;
}