.line-scan {
  width: 100%;
  height: 100%;
}

.line-scan .grid-bg {
  width: 100%;
  height: 100%;
  background-image: url('../../Assets/line_grid.png');
}

.line-scan .info {
  position: absolute;
  top: 230px;
  height: 40px;
  width: 400px;
  left: 40px;
  border-radius: 10px;
  text-align: center;
  border: 2px solid white;
  font-size: 14px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  vertical-align: middle;
}

#canvas {
  position: absolute;
  top: 40px;
  left: 40px;
  width: 400px;
  height: 170px;
  z-index: 500;
}

.average-calc {
  width: 100%;
  height: 100%;
  background: #000;
  z-index: 1000;
  position: absolute;
  top: 0px;
  left: 0px;
  color: white;
}