.status-bar {
  position: fixed;
  bottom: 0px;
  left: 0px;
  height: 30px;
  width: 100vw;
  background: rgba(0, 0, 0, 0.501);
  /* background: linear-gradient(to bottom, rgba(242, 242, 242, 1) 0%, rgba(226, 226, 226, 1) 40%); */
  color: white;
  z-index: 40000;
}

.status-bar .fas {
  font-size: 18px;
  margin: 7px 15px 0px 0px;
  float: right;
}

.status-bar .menu-title {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -40%);
  font-size: 16px;
  color: rgb(255, 255, 255);
}

.status-bar .time {
  float: left;
  margin-top: 7px;
  margin-left: 5px;
  font-size: 16px;
  color: rgb(255, 255, 255);
}



@keyframes gpsicon {
  0% {
    opacity: 0.5;
  }
  50%{
    opacity: 1;
  }
  100% {
    opacity: 0.5;
  }
}

.status-bar-indicator{
  width: 30px;
  height: 30px;
  float: left;
  margin: 0px 0px 0px 5px;
}

.status-bar-indicator img{
  width: 26px;
  margin-top: 2px;
}