.factory-reset {
  width: 100%;
  height: 91%;
  background: rgb(49, 49, 49);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.factory-reset .section {
  width: 450px;
  height: 130px;
  background: rgb(110, 110, 110);
  border-radius: 20px;
  box-shadow: inset 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#sec1 {
  border-bottom-right-radius: 0px;
  border-bottom-left-radius: 0px;
}

#sec2 {
  border-top-right-radius: 0px;
  border-top-left-radius: 0px;
}

.factory-reset .title {
  font-size: 14px !important;
}

.factory-reset .info {
  text-align: center;
  font-size: 13px;
}

.section .fas {
  position: absolute;
  top: 70px;
  left: 20px;
  font-size: 40px;
  color: red;
  opacity: 0.4;
}

.section .reset-button {
  text-align: center;
  border: 2px solid rgb(0, 0, 0);
  width: fit-content;
  padding: 3px 10px 3px 10px;
  margin-top: 12px;
  color: white;
  border-radius: 10px;
  font-weight: 600;
}

.section .selected-reset-button {
  transition: all .5s;
  transform: scale(1.1);
  background: red;
}

.soft-reset-popup {
  position: absolute;
  width: 360px;
  height: 100px;
  background: rgb(36, 161, 19);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
  border-radius: 20px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  
}

.soft-reset-popup::before{
  position: absolute;
  background: rgba(0, 0, 0, 0);
  content: " ";
  z-index: -1;
  top: -100px;
  left: -80px;
  width: 520px;
  height: 330px;

}

.soft-reset-warning{
  color: white;
  text-align: center;

}

.soft-popup-progress-bar{
  width: 100%;
  height: 30px;
  background: #000;
  margin-top: 20px;
  border-radius: 10px;
  overflow: hidden;
  border: 2px solid white;
}

.moving-bar{
  background: red;
  width: 0%;
  height: 30px;
  background: linear-gradient(0deg, rgb(148, 1, 1) 0%, rgba(255,0,0,1) 100%);
  transition: all 200ms;
  transition-timing-function: linear;
}


.hard-reset-deleting{
  position: absolute;
  top:0px;
  width: 100vw;
  height: 100vh;
  background: white;
  z-index: 50005;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hard-reset-deleting-title{

}

.hard-reset-deleting-image{
  width: 40px;
  margin-top: 10px;
}

.factory-reset .file-delete-button{
  color: white;
  border: none;
  background: rgb(29, 29, 29);
}

.factory-reset .file-delete-button.popup-selected{
  background: red;
}