.battery{
  width: 65px;
  height: 30px;
  float: right;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-right: 5px;
  margin-right: 15px;
}

.battery-outer{
  width: 25px;
  height: 10px;
  border: 2px solid white;
}

.battery-outer::after{
  content: " ";
  width: 3px;
  height: 8px;
  background: white;
  position: absolute;
  right: 12px;
  top: 11px;
}

.battery-outer .battery-inner{
  background: white;
  width: 0%;
  height: 100%;
  /* transition: all 1500ms; */
}

.battery .fas{
  color: black;
  position: relative;
  transform: rotate(270deg);
  font-size: 12px;
  left: 7px;
  top: -9px;
}

.battery-level{
  font-size: 12px;
  margin-right: 5px;
  margin-top: 1px;
}