.setup{
    position: absolute;
    width: 100vw;
    height: 100vh;
    background: #000;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 50000 !important;
}

.setup-panel{
    width: 90vw;
    height: 90vh;
    background: white;
    z-index: inherit;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.panel-welcome{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    flex-direction: column;
}

.panel-welcome .panel-welcome-title{
    font-size: 30px;
    font-weight: 600;
    animation: flip 1.53s cubic-bezier(0.165, 0.84, 0.44, 1) 1s infinite;
}

@keyframes flip{
    0%{
        transform: rotateX(0deg)
    }
    100%{
        transform: rotateX(360deg)
    }
}

.panel-welcome .panel-welcome-content{
    margin-top: 10px;
    text-align: center;
}

.panel-title{
    font-weight: 600;
    font-size: 24px;
}

.panel-info{

}

.panel-selection{
    border: 2px solid black;
    width: 250px;
    text-align: center;
    padding: 10px 20px 10px 20px;
    margin-top: 20px;
    border-radius: 10px;
}

.panel-selection::after{
    content: '>';
    position: absolute;
    right: 110px;
}

.panel-selection::before{
    content: '<';
    position: absolute;
    left: 110px;
}

.setup .stepper{
    position: absolute;
    z-index: 50005;
    width: 350px;
    border-top: 3px dashed rgb(124, 124, 124);
    top: 30px;
    display: none;
    justify-content: space-between;
}

.setup .stepper .stepper-item{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    transform: translateY(-10px);
    width: 30px;
    
}

.setup .stepper .stepper-item .index{
    font-weight: 600;
    font-size: 12px;
    background: rgb(124, 124, 124);
    width: 13px;
    height: 14px;
    border-radius: 50%;
    color: white;
    text-align: center;
    padding: 2px;
}

.setup .stepper .stepper-item .name{
    font-size: 11px;
    text-align: center;
    margin-top: 4px;
}

.setup .stepper-selected{
    background: rgb(245, 111, 2) !important;
    border: 2px solid rgb(78, 78, 78);
    transform: translateY(-2px)
}

.setup .stepper-passed{
    background: rgb(64, 160, 0) !important;
    border: 2px solid rgb(78, 78, 78);
    transform: translateY(-2px)
}

.setup .setup-preloader{
    margin-top: 10px;
    width: 30px;
    animation: setupRotate 1s linear infinite;
}

@keyframes setupRotate{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}