.resistivity-auto {
  background-color: rgba(255, 255, 255, 0.774);
  width: 100vw;
  height: 91%;
  transform: scale(1.5);
  transition: all 1s;
  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
}

.resistivity-manual {
  background-color: rgba(255, 255, 255, 0.705);
  width: 100vw;
  height: 91%;
  transform: scale(1.5);
  transition: all 1s;
  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
}

.resistivity-intro-video {
  background-image: url("../../Assets/autores2.png");
  background-size: cover;
  width: 320px;
  height: 120px;
  margin: 0px auto;
  position: relative;
  top: 20px;
  background-position-y: -30px;
}



.resistivity-intro-text {
  width: 320px;
  margin: 0px auto;
  position: relative;
  top: 40px;
  text-align: center;
  font-size: 15px;
  color: rgb(0, 0, 0);
}

.start-button {
  background: rgb(36, 161, 19);
  width: 180px;
  height: 30px;
  line-height: 30px;
  margin: 0px auto;
  position: relative;
  border-radius: 10px;
  top: 60px;
  text-align: center;
  font-size: 20px;
  color: white;
  animation: ani-wave 1s infinite;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.85);
}

@keyframes ani-wave {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.resistivity-auto-scan-video {
  position: fixed;
  right: 0;
  bottom: 0;
  top: 0;
  left: 0;
  min-width: 100%; 
  min-height: 100%;
}

.resistivity-auto-scan-video video {
  position: fixed;
  right: 0;
  bottom: 0;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 9000000;
}

.resistivity-manual-scan-video {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
}

.resistivity-manual-scan-video video {
  background-size: cover;
  width: 450px;
  z-index: 9000000;
}

/* ---------- */

.lines {
  width: 80%;
  margin: 0px auto;
  position: relative;
  top: 35px;
  background: rgb(87, 87, 87);
  padding: 10px;
  border-radius: 20px;
}

.lines .line {
  margin-bottom: 5px;
  background: rgb(39, 39, 39);
  width: 80px;
  text-align: center;
  padding: 5px 15px 5px 15px;
  color: white;
}

.lines .line:first-child {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.lines .line:nth-child(6) {
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  margin-bottom: 0px;
}

.line.selected {
  color: #fff !important;
  background: rgb(36, 161, 19) !important;
  transition-duration: 0.7s !important;
  transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 2.275);
}

.lines .line:nth-child(1)::before{
  content: "";
  position: absolute;
  width: 180px;
  height: 4px;
  background: inherit;
  left: 160px;
  top: 40px;
  z-index: 3;
}



.lines .line:nth-child(2)::before{
  content: "";
  position: absolute;
  width: 4px;
  height: 140px;
  background: inherit;
  left: 340px;
  top: 40px;
  z-index: 3;
}

.lines .line:nth-child(3)::before{
  content: "";
  position: absolute;
  width: 180px;
  height: 4px;
  background: inherit;
  left: 160px;
  top: 176px;
  z-index: 3;
}

.lines .line:nth-child(4)::before{
  content: "";
  position: absolute;
  width: 4px;
  height: 140px;
  background: inherit;
  left: 160px;
  top: 40px;
  z-index: 3;
}

.lines .line:nth-child(5)::before{
  content: "";
  position: absolute;
  width: 223px;
  height: 4px;
  background: inherit;
  left: 140px;
  top: 107px;
  transform: rotate(37deg);
  z-index: 1;
}

.lines .line:nth-child(6)::before{
  content: "";
  position: absolute;
  width: 223px;
  height: 4px;
  background: inherit;
  left: 140px;
  top: 107px;
  transform: rotate(143deg);
  z-index: 1;
}

.lines .line:nth-child(1)::after{
  content: "A";
  position: absolute;
  left: 241px;
  top: 13px;
  z-index: 3;
  background: inherit;
  padding: 2px 4px 2px 4px;
  border-radius: 4px;
}

.lines .line:nth-child(2)::after{
