.high-voltage-warning{
  background: #000;
  width: 300px;
  height: 220px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  color: white;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
}

.high-voltage-warning img{
  width: 100px;
}

.high-voltage-warning .high-voltage-text{
  text-align: center;
  padding: 20px;
  font-size: 14px;
  font-weight: 900;
}

.manual-scan{
  position: absolute;
  width: 100vw;
  height: 91vh;
  top: 0px;
  background: #000;
  color: white;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 18px;
}

.manual-preloader{
  width: 300px;
  height: 30px;
  border: 2px solid white;
  margin-top: 10px;
  border-radius: 10px;
  overflow: hidden;
}

.manual-preloader .manual-preloader-solid{
  width: 0%;
  height: 30px;
  background: linear-gradient(0deg, rgba(9,75,22,1) 0%, rgba(87,255,123,1) 77%, rgba(0,215,47,1) 100%);
  transition: all 1s;
}