.preloader {
  width: 100vw;
  height: calc(100vh - 30px);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 10px;
}

.bar {
  width: 220px;
  height: 25px;
  background: green;
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(143, 196, 0, 1) 5%, rgba(143, 196, 0, 1) 17%, rgba(255, 255, 255, 0) 23%);
  animation: loader 1s infinite;
  animation-timing-function: linear;
  -o-animation-direction: reverse;
  background-size: 200%;
  border-radius: 10px;
  border: 3px solid white;
}

.preloader-title {
  margin: 10px;
  color: white;
  font-size: 18px;
  text-transform: uppercase;
}

@keyframes loader {
  0%{
    background-position: 500%;
  }
	100% {
		background-position: 300%;
	}
}