/* İki menü birden */
.menu-panel{
  height: 220px;
  width: 210px;
  position: absolute;
  top: 35px;
  left: 5vw;
  display: flex;
  flex-direction: column;
  /* background: red; */
  transition: all 0.5s;
  transform-style: flat;
  backface-visibility: hidden;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0px 0px 13px 0px rgba(0, 0, 0, 0.85);
}


.menu-button {
  background: rgb(68, 68, 68);
  width: 195px;
  height: 50px;
  font-size: 16px;
  line-height: 100%;
  padding-right: 15px;
  border-bottom: 1px solid rgb(139, 139, 139);
  color: rgb(91, 173, 201);
  transition: all 0.1s;
  float: right;
  text-align: right;
  -webkit-transform-style: preserve-3d;
  z-index: 5;
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu-container{
  height: 100vh;
}

.menu-button:nth-last-child(2) {

  border-width: 1px;
  border-color: rgba(255, 255, 255, 0) !important;
}

.menu-button span {
  color: rgb(255, 255, 255);
  padding-right: 20px;
  padding-left: 20px;
  text-align: left;
}

.menu-container span {
  padding-left: 28px;
}

.menu-container span {
  padding-left: 28px;
}

.selected {
  /* background-color: rgb(36, 161, 19) !important; */
  background-color: var(--selected-color) !important;
  /* box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.85); */
  /* box-shadow: var(--box-shadow) !important; */
  transition: all 500ms;
  /* transition-delay: .2; */
  color: var(--selected-icon-color) !important;
  border-bottom: 1px solid var(--selected-color) !important;
  transform-style: flat;
  z-index: 8;
}


.menu-button .fa-angle-right {
  float: right;
  margin: 16px 15px 0px 0px;
}

.reveal{
  /* animation: slide-from-right 0.5s ease-in 1 backwards 0.1s; */
  opacity: 1 !important;
}

@keyframes slide-from-right{
  0%{
    opacity: 0;
    /* transform: translateX(var(--anan)) scale(0.4); */
  }
  98%{
    opacity: 1;
    /* transform: translateX(0px); */
    
  }
}

.menu-icon{
  position: absolute;
  width: 150px;
  z-index: 1000;
  top: 40px;
  right: 20px;
  transition: opacity 0.2s;
  /* animation: scaleupdown 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) infinite; */
}

@keyframes scaleupdown{
  0%{
    transform: scale(1);
  }50%{
    transform: scale(1.1);
  }100%{
    transform: scale(1);
  }
}