.file-browser {
  width: 100vw;
  height: calc(100vh - 30px);
  background: rgba(68, 68, 68, 0.253);
  transition: all 400ms;
  transform: scale(3);
  opacity: 0;
  overflow: hidden;
}

.title {
  color: white;
  font-weight: 600;
  text-align: center;
  padding-top: 10px;
  padding-bottom: 10px;
}

.file-container {
  margin: 0px auto;
  background-color: rgb(99, 99, 99);
  width: 95vw;
  height: 85vh;
  border: 2px solid rgb(59, 59, 59);
  overflow: hidden;
  box-shadow: inset 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  position: relative;
  margin-top: 2.5vh;
}

.file-list {
  overflow: hidden;
  transition: transform 0.3s;
  transition-timing-function: cubic-bezier(0.175, 0.385, 0.32, 1.375);
  /* transform: translateY(-50px); */
}

.fa-folder-open {
  padding-right: 8px;
}

.fa-file-invoice {
  padding-right: 8px;
}

.file {
  overflow: hidden;
  width: 100%;
  /* background: red; */
  height: 40px;
  line-height: 40px;
  padding-left: 15px;
  border-bottom: 1px solid black;
  color: #fff;
  transition: all 300ms;
  background: linear-gradient(0deg, rgb(75, 75, 75) 0%, rgb(114, 113, 113) 100%);
}

.file.selected {
  background: rgb(36, 161, 19) !important;
  border-bottom: 1px solid black !important;
  transition: all 600ms;
  color: #fff !important;
  z-index: 5 !important;
}

.file.selected::after {
  content: ">";
  float: right;
  margin-right: 172px;
  margin-top: 10px;
  background: rgb(54, 54, 54);
  line-height: 10px;
  padding: 5px;
  border-radius: 50%;
  font-weight: 600;
  animation: arrow-ani 1s infinite 400ms;
  opacity: 0.4;
}

.file-control {
  width: 140px;
  height: 273px;
  background: rgb(68, 68, 68);
  float: right;
  position: absolute;
  top: 0px;
  right: 0px;
  /* background: rgb(36, 161, 19); */
  border-left: 1px solid black;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.file-control-button {
  font-size: 17px;
  color: #fff;
  width: 90px;
  height: 90px;
  background-color: rgb(99, 99, 99);
  border-radius: 15px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  border: 1px solid rgb(46, 46, 46);
  box-shadow: inset 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
}

.file-control-button.selected {
  background: rgb(36, 161, 19) !important;
  color: white !important;
}

.file-control-button .fas {
  font-size: 30px;
  padding: 5px;
}

@keyframes arrow-ani {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(5px);
  }
  100% {
    transform: translateX(0px);
  }
}

.file-delete-popup {
  position: absolute;
  width: 360px;
  height: 130px;
  background: rgb(49, 49, 49);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
  border-radius: 20px;
  color: white;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
  justify-content: center;
  flex-direction: column;
  align-items: center;
  display: none;
}

.button-container {
  display: flex;
  width: 100%;
  justify-content: space-around;
}

.file-delete-warning {
  font-weight: 600;
}

.file-delete-button {
  text-align: center;
  width: 60px;
  margin-top: 20px;
  ;
  border: 2px solid white;
  padding: 2px 20px 2px 20px;
  border-radius: 5px;
}

.file-delete-button.popup-selected {
  background: red;
}

.file-browser .preview {
  width: 200px;
}

.file-browser .preview-holder {
  overflow: hidden;
  background: rgb(32, 32, 32);
  width: 120px;
  height: 110px;
  display: flex;
  justify-content: center;
  border: 2px solid rgb(201, 201, 201);
  border-radius: 10px;
}

.favorite {
  position: absolute;
  right: 180px;
  margin-top: 11px;
  opacity: 0.4;
  transition: all 700ms;
  transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transform: scale(0.8) rotate(0deg);
}

.favorite-enabled{
  color: yellow;
  opacity: 1;
  transform: scale(1.2) rotate(360deg);
}

